<script>
	import Header from './common/Header.vue'
	import {mapGetters , mapActions} from 'vuex'
	import ResumeItem from './common/ResumeItem.vue'
	import search from './common/SearchInput'
	import {localStorageKey} from '../../static/dicConfig'
	export default {
	  name:'ResumeList',
	  components:{
	     'common-header': Header,
	     'resume-item': ResumeItem,
	     'search': search
	  },
	  data () {
	    return {
	    	titleName:this.$route.query.titleName,
	    	timeSlot:'09:00-09:20',
	    	isShowSearchText:false,
	    	params: {
		    	pageIndex:1,
		    	pageSize: 5,
		    	roomId: this.$route.query.roomId,
		    	signStatus: 0,
		    	name:'',
		    	isSearch:false,
		    	isClearCache:true
	    	},
	    	loadingMsg:'上拉显示更多内容',
	    	searchRecord: [],
	  		isShowSearchRecord:false,
	  		searchValue:'',
	  		isNotActive:0
	    }
	  },
	  created(){
	  	this.getExamDetailList(this.params);
	  },
	  computed:{
	  	...mapGetters(['examDetailList'])
	  },
	  methods:{
	  	...mapActions(['getExamDetailList']),
	    clickTab(flag){
	    	this.isNotActive = flag;
	    	this.loadingMsg = '上拉显示更多内容';
	    	this.params.signStatus = flag;
	    	this.params.pageIndex =1;
	    	this.params.isClearCache = true;
	  		this.getExamDetailList(this.params);
	    },
	    clickSearch(flag){
	    	this.isShowSearchText = flag;
	    	this.isShowSearchRecord = flag;
	    	this.params.name = '';
	    	this.params.isSearch = false;
	    	if (flag) {
	    		this.getSearchRecord();
	    	}
	    	else{
	    		this.params.pageIndex = 1;
	    		this.params.signStatus = 0;
	    		this.params.isClearCache = true;
	    		this.getExamDetailList(this.params);
	    	}
	    },
	    changeNextPageStart(ev){
	    },
	    changeNextPageMove(ev){
	    },
	    changeNextPageEnd(ev){
	    	const eleScrollTop = window.document.documentElement.scrollTop;
	    	const eleScrollHeight = window.document.documentElement.scrollHeight;
	    	const eleClientHeight = window.document.documentElement.clientHeight;
	    	const isClearCache = false;
	    	if (eleScrollTop + eleClientHeight >= eleScrollHeight-5) {
	    		this.params.pageIndex++;
	    		const {pageIndex , pageSize , roomId , signStatus , name} = this.params;
	    		if (pageIndex  > Math.ceil(this.examDetailList.total/pageSize)) {
	    			this.loadingMsg = '没有更多数据了'
	    			return;
	    		}
	    		this.getExamDetailList({pageIndex , pageSize , roomId , signStatus , isClearCache , beforeCallBack: ()=>{
	    			this.loadingMsg = '正在加载...';
	    		} , successCallBack: ()=>{
	    			this.loadingMsg = '上拉显示更多内容';
	    		}});
	    	}
	    },
	    toSearchRecord(){
	    	this.isShowSearchRecord = true;
	    	this.getSearchRecord();
	    },
	    searchSubmit(text){
	    	text = text.trim();
	    	//搜索条件全为空格不搜索
	    	if (/^\s*$/.test(text)) {
	    		return;
	    	}
	    	this.params.name=text;
	    	this.isShowSearchRecord = false;
	    	this.params.pageIndex =1;
	    	this.params.isSearch = true;
	    	this.params.isClearCache = true;
	    	this.getExamDetailList(this.params);
	    	this.saveSearchRecord(text);
	    },
	    changeSearchRecord(value){
	    	this.searchValue = value;
	    	this.searchSubmit(value);
	    },
	            /**
         * @method 保存搜索记录
         */
        saveSearchRecord(txt) {
            let old = JSON.parse(localStorage.getItem(localStorageKey.search_resume_key));
            if (old && Array.isArray(old)) {
            	//记录存在，则不添加
            	if (old.indexOf(txt) > -1) {
            		return;
            	}
                if (old.length >= 5) {
                    //弹出第一个 先进先出
                    old.shift();
                }
            }
            else {
                old = [];
            }
            old.push(txt);
            localStorage.setItem(localStorageKey.search_resume_key, JSON.stringify(old));
            this.getSearchRecord();
        },
        /**
         * @method 删除搜索记录
         */
        delSearchRecord(e,index) { 
        	e.stopPropagation();
            let old = JSON.parse(localStorage.getItem(localStorageKey.search_resume_key));
            if (old && Array.isArray(old)) {
               old.splice(index-1,1);
            }
            localStorage.setItem(localStorageKey.search_resume_key, JSON.stringify(old));
            this.getSearchRecord();
        },
        /**
         * @method 获取搜索记录
         */
        getSearchRecord() {
            let r = JSON.parse(localStorage.getItem(localStorageKey.search_resume_key));
            if (r && Array.isArray(r)) {
                r.reverse();
            }
            else{r=[];}
            this.searchRecord = r;
        },
	  }
	}
</script>
<template>
	<div class="resume-list" ref="list">
		<common-header :title="titleName"></common-header> 
		<div class="search-tab clearfix" :class='isShowSearchText ? "hidden-div" : ""'>
			<div class="alltab" :class="params.signStatus === 0 ? 'active' : ''" @click="clickTab(0)">全部</div>
			<div class="notsign" :class="params.signStatus === -1  ? 'active' : ''" @click="clickTab(-1)">
				未签到
				<div class="notsignMessage" v-if="examDetailList.noSign"><p>{{examDetailList.noSign}}</p></div>
			</div>
			<i class="fl" @click="clickSearch(true)"></i>
		</div>
		<div class="search-text clearfix" :class='isShowSearchText ? "" : "hidden-div"'>
			<search 
				class="fl text" 
				placeholder="搜索候选人" 
				:searchValue="searchValue" 
				:autofocus="true" 
				:width="12.5" 
				:searchSubmit="searchSubmit" 
				:focusCallBack = "toSearchRecord"
			/>
			<div class="cancel fl" @click="clickSearch(false)">取消</div>
		</div>
		<div class="content" v-if="!isShowSearchRecord">
			<div class="exam-list"  @touchend="changeNextPageEnd($event)">
				<resume-item 
					v-for="(key , index) in Object.keys(examDetailList.data)" 
					:itemList="examDetailList.data[key]" 
					:key = "index"
					:isAll = "isNotActive === 0 ? true : false"
				/>
				<div v-if="Object.keys(examDetailList.data).length === 0">
				<div class="fill-block"></div>
					<img src="../assets/images/icon_no_data.png">
				</div>
			</div>
			<div class="loading" v-if="Object.keys(examDetailList.data).length >= 5">
				<span>{{loadingMsg}}</span>
			</div>
		</div>
		<div class="search_recent" :class="{no_data:searchRecord.length<=0}" v-if="isShowSearchRecord">
            <p class="search_recent_tite">最近搜索</p>
            <ul class="search_recent_list">
                <li class="search_recent_item" v-for="(item,index) in searchRecord" v-bind:key="index" @click="changeSearchRecord(item)">
                    <span class="content">{{item}}</span>
                    <i class="icon_del" @click="delSearchRecord($event,searchRecord.length-index)"></i>
                </li>
            </ul>
            <div class="no_data_warp">
                <div class="no_data_img">
                </div>
                <p>暂无搜索记录</p>
            </div>
        </div>
	</div>
</template>
<style lang='less' scoped>
	@import '../assets/css/resumeList.less';
</style> 


