<script>
	import { doMain } from '../../../static/dicConfig.js'
	export default{
		name:'ResumeItem',
		data(){
			return{
				imgPathObj:{
		    		0: require('../../assets/images/undetermined.png'),
		    		1: require('../../assets/images/pass.png'),
		    		2: require('../../assets/images/noPass.png'),
    			},
    			imgMainPath:doMain.url,
    			defaultSrc:require('../../assets/images/defaultPhoto.png'),
    			dataSrc:''
			}	
    	},
    	props:{
    		itemList:{
    			type:Array,
    			default(){
    				return {}
    			}
    		},
    		isAll:{
    			type:Boolean,
    			default(){
    				return false;
    			}
    		}
    	},
    	methods:{
    		loadImage(path){
    			const image = new Image();
    			image.src = path;
    			image.onload=()=>{
    				this.dataSrc = path;
    			}
    			return true
    		}
    	}
	}
</script>
<template>
	<div>
		<div v-for="item in  itemList" >
			<router-link :to = "{
					name: 'ResumeDetails' , 
					query: {campusId:item.CampusId , postID: item.PostId , roomId: item.RoomId}
				}"
			>			
				<div class="tip">{{item.TimeRange}}</div>
				<div class="item clearfix">
					<img :src="item.PhotoSrc && item.PhotoSrc != 'null' ? (imgMainPath + item.PhotoSrc) : defaultSrc" class="fl photo">
					<div class="fl item-left">
						<div class="person-info-top clearfix">
							<span class="name fl">{{item.Name}}</span>
							<span class="sex fl">{{item.Gender}}</span>
							<span class="education fl">{{item.Degree}}</span>
							<!-- <span class="school-level fl">{{item.SchoolType}}</span> -->
							<span class="sign fl" v-if="isAll">{{item.SignStatus === 1 ? '已签到' : '未签到' }}</span>
							<span class="order fl">{{item.SeatNo < 10 ? '0' + item.SeatNo : item.SeatNo}}</span>
						</div>
						<div class="person-info-middle">
							<span>{{item.SchoolName}}</span>
							<span>|</span>
							<span>{{item.Major}}</span>
						</div>
						<div class="person-info-bottom clearfix">
							<span class="fl">{{item.CommentCount}}条评论</span>
							<div class="contact fl clearfix" v-if="!isAll">
								<a class="fl" :href="'tel:' + item.Phone"></a>
								<a class="fl" :href="'sms:' + item.Phone"></a>
							</div>
						</div>
					</div>
					<img :src="imgPathObj[item.SiftStatus]" class="pass-flag">
				</div>
			</router-link>
		</div>
	</div>
</template>
<style lang="less" scoped>
	@r:40rem;
	.tip{
		font-size: 22/@r;
		color: #707c7f;
		line-height: 50/@r;
		margin-left: 30/@r;
		i{
			display: block;
			margin-left: 35/@r;
			margin-right: 5/@r;
			margin-top: 6/@r;
			width: 25/@r;
			height:25/@r; 
			background: url('../../assets/images/icon.png') no-repeat;
			background-size: 610/@r;
			background-position: -76/@r -10/@r;
		}
	}
	.item{
		position: relative;
		background-color: white;
		padding:30/@r 0 30/@r 30/@r;
		color: #99a5a7;
		font-size: 20/@r;
		img{
			width: 120/@r;
		}
		.photo{
			width: 90/@r;
			height: 110/@r;
		}
		.item-left{
			margin-left: 25/@r;
			.person-info-top{
				position: relative;
				margin-top: -10/@r;
				margin-bottom: 15/@r;
				font-size: 24/@r;
				span{
					margin-right: 10/@r;
/*					text-overflow: ellipsis;*/
					white-space: nowrap;
				}
				.name{
					font-size: 28/@r;
					font-weight: 600;
					margin-right: 10/@r;
					color: black;
					margin-right: 25/@r;
					max-width: 144/@r;
				}
				.sex,.education,.school-level{
					display: inline-block;
					color: #707c7f;
					font-size: 24/@r;
					overflow: hidden;
					margin-top: 3.6/@r;
				}
				.school-level{
					max-width: 46/@r;
				}
				.education{
					max-width: 80/@r;
				}
				.sign{
					color: #ff3600;
					border:1px solid #ff3600;
					padding: 0 8/@r;
					border-radius: 15/@r;
					font-size: 16/@r;
					margin-top: 3.6/@r;
				}
				.order{
					position: absolute;
					margin-top: 3.6/@r;
					left: 420/@r;
					color: #f29c00;
				}
			}
			.person-info-middle{
				margin-bottom: 10/@r;
			}
			.person-info-bottom{
				.contact{
					a{
						display: block;
						width: 30/@r;
						height: 28/@r;
					}
					a:first-child{
						background:url('../../assets/images/icon.png') no-repeat;
						background-position: -236/@r -112/@r;
						background-size: 720/@r;
						margin-left: 288/@r;
						margin-top: -2/@r;
					}
					a:last-child{
						background:url('../../assets/images/icon.png') no-repeat;
						background-position: -300/@r -112/@r;
						background-size: 720/@r;
						margin-left: 30/@r;
						margin-top: -2/@r;
					}
				}
			}
		}
		.pass-flag{
			position: absolute;
			left: 584/@r;
		    top: 0;
		    width: 57/@r;
		    height: 61/@r;
		}
	}
</style>


